import React from "react";
import styles from "./index.module.scss";

const TabButton = ({
  Icon,
  name,
  status,
  isNotLast,
  handleActiveTab,
  currentTab,
  showPreview
}) => {

  return (
    <div
      className={`${styles.container} ${currentTab && styles.inProgress}`}
      onClick={handleActiveTab}
    >
      <div className={`d-flex align-items-center ${styles.button}`}>
        <div
          className={`${styles.logoBox} ${showPreview ? currentTab ?
            styles.inProgressBox : "" :
            status === "IN PROGRESS"
              ? styles.inProgressBox
              : status === "COMPLETED"
                ? styles.completedBox
                : ""
            }`}
        >
          <Icon />
        </div>
        <div className={styles.content}>
          {!showPreview && <span
            className={`${styles.status} ${
              status === "IN PROGRESS"
                ? styles.inProgressStatus
                : status === "COMPLETED"
                  ? styles.completedStatus
                  : ""
              }`}
          >
            {status}
          </span>}
          <span className={styles.name}>{name}</span>
        </div>
      </div>
      {isNotLast && <span className={styles.barrier}></span>}
    </div>
  );
};

export default TabButton;
